<template>
  <div id="login">
    <el-main id="main">
      <el-row type="flex" class="row-bg" justify="center">
        <el-col :xs="8" :sm="9" :md="8" :lg="6" :xl="5">
          <el-card class="box-card">
            <div solt="header" class="header">
              <span class="item">登录</span>
            </div>
            <div id="form">
              <!-- 登录表单 -->
              <el-form :model="login" status-icon :rules="rule" ref="login">
                <el-form-item prop="username">
                  <el-input placeholder="请输入用户名" prefix-icon="el-icon-user"
                            v-model="login.username" auto-complete="off"/>
                </el-form-item>
                <el-form-item prop="password">
                  <el-input placeholder="请输入密码" prefix-icon="el-icon-key"
                            type="password" v-model="login.password" auto-complete="off"/>
                </el-form-item>
                <el-form-item>
                  <el-checkbox class="check" v-model="checked">记住我</el-checkbox>
                </el-form-item>
                <el-form-item>
                  <el-button class="btn" type="primary" @click="submitForm('login')">登录</el-button>
                </el-form-item>
              </el-form>
              <div>
                <p><router-link to="/register" class="tips">还没有账号？点我去注册</router-link></p>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-main>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    const checkUsername = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名'))
      } else {
        callback()
      }
    }
    const checkPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        callback()
      }
    }
    return {
      checked: false,
      login: {
        username: '',
        password: '',
        remember: ''
      },
      rule: {
        username: [
          {validator: checkUsername, trigger: 'blur'}
        ],
        password: [
          {validator: checkPassword, trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    submitForm (login) {
      this.$refs[login].validate((valid) => {
        if (valid) {
          // 提交表单
          this.$http.get('http://127.0.0.1:8080/api/user/login',
            {
              params: {
                name: this.login.username,
                password: this.login.password
              }
            }).then(function (res) {
            // 解析返回数据
            const resbody = res.body
            if (resbody.success) {
              // 跳转到home页面
              this.$router.push({
                path: '/home',
                name: 'Home',
                params: {
                  name: this.login.username,
                  id: resbody.userid
                }
              })
            } else {
              alert(resbody.message)
            }
          }, function (res) {
            console.log(res.status)
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style scoped>
  body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  }

  #login {
    background: url("../assets/login.png");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -999;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  #main {
    font-family: Helvetica, sans-serif;
    margin-top: 8%;
    text-align: center;
  }

  .header {
    border-bottom: 1px solid #ebeef5;
    margin-bottom: 20px;
    padding: 15px 20px;
  }

  .header > .item {
    font-size: 23px;
    color: #3a8ee6;
  }

  .btn {
    width: 100%;
    border-radius: 28px;
  }

  #form {
    padding: 0 30px;
  }

  .check {
    float: left;
  }

  .tips {
    font-size: 13px;
    text-align: right;
    color: #409EFE;
    text-decoration: none;
  }

  .tips:hover {
    color: #3a8ee6;
  }
</style>



