<template>
  <div>
    <el-row>
      <el-col :span="4">
        <div style="margin-top: 40px">
          <i class="el-icon-user"></i>
          {{ user.name }}
        </div>
      </el-col>
      <el-col :span="16">
        <h1>{{ title }}</h1>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="20" :offset="2">
        <el-divider></el-divider>
        <div>
          <el-container style="height: 500px; border: 1px solid #eee">
            <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
              <el-menu :default-openeds="['1']">
                <el-submenu index="1">
                  <template slot="title">
                    传感器列表
                    <i class="el-icon-circle-plus-outline" @click="addSensor"></i>
                  </template>
                  <el-menu-item-group>
                    <el-menu-item
                      v-for="(sensor,index) in sensor_list"
                      :key="sensor.id"
                      @click="selectSensor(sensor,index+1)"
                      style="color: #606266">
                      {{ sensor.type }}传感器{{ index+1 }}
                      <i class="el-icon-setting" @click="sensorInfo(sensor,index+1)"></i>
                    </el-menu-item>
                  </el-menu-item-group>
                </el-submenu>
              </el-menu>
            </el-aside>

            <el-container>
              <el-header>
                <span>{{ chartTitle }}</span>
              </el-header>

              <el-main>
                <div>
                  <line-chart :sensor="selectedSensor" v-show="chartVisible"></line-chart>
                </div>
              </el-main>
            </el-container>
          </el-container>

          <!--传感器信息添加对话框-->
          <el-dialog
            :title="dialog1Title"
            :visible.sync="dialogVisible1"
            width="30%"
            :before-close="handleClose">
            <el-form ref="newSensor" :model="newSensor" :rules="rules" label-width="100px">
              <el-form-item label="传感器ID" prop="id">
                <el-input v-model="newSensor.id"></el-input>
              </el-form-item>
              <el-form-item label="传感器类型" prop="type">
                <el-select v-model="newSensor.type" placeholder="请选择传感器类型">
                  <el-option label="温度" value="温度"></el-option>
                  <el-option label="湿度" value="湿度"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="经度" prop="longitude">
                <el-input v-model="newSensor.longitude"></el-input>
              </el-form-item>
              <el-form-item label="纬度" prop="latitude">
                <el-input v-model="newSensor.latitude"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button type="primary" @click="submitSensor('newSensor')">添加</el-button>
              <el-button @click="resetSensor('newSensor')">重置</el-button>
            </span>
          </el-dialog>

          <!--传感器信息设置对话框-->
          <el-dialog
            :title="dialog2Title"
            :visible.sync="dialogVisible2"
            width="30%"
            :before-close="handleClose">
            <el-form ref="selectedSensor" :model="selectedSensor" label-width="100px" :disabled="true">
              <el-form-item label="传感器ID">
                <el-input v-model="selectedSensor.id"></el-input>
              </el-form-item>
              <el-form-item label="传感器类型">
                <el-select v-model="selectedSensor.type" placeholder="请选择传感器类型">
                  <el-option label="温度" value="温度"></el-option>
                  <el-option label="湿度" value="湿度"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="经度">
                <el-input v-model="selectedSensor.longitude"></el-input>
              </el-form-item>
              <el-form-item label="纬度">
                <el-input v-model="selectedSensor.latitude"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible2 = false">取 消</el-button>
              <el-button type="primary" @click="submitChange">确 定</el-button>
            </span>
          </el-dialog>
        </div>
        <el-divider></el-divider>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import LineChart from './LineChart'
export default {
  name: 'Home',
  components: {LineChart},
  data () {
    return {
      title: '物联网数据管理平台',
      user: {
        id: this.$route.params.id,
        name: this.$route.params.name
      },
      chartTitle: '传感器数据分析图',
      dialogVisible1: false,
      dialogVisible2: false,
      dialog1Title: '添加传感器',
      dialog2Title: '',
      chartVisible: false,
      newSensor: {
        id: '',
        type: '',
        longitude: '',
        latitude: ''
      },
      rules: {
        id: [
          { required: true, message: '请输入传感器ID', trigger: 'blur' },
          { min: 11, max: 11, message: '请输入11位长度的传感器ID', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '请选择传感器类型', trigger: 'change' }
        ],
        longitude: [
          { required: true, message: '请输入传感器经度信息', trigger: 'blur' }
        ],
        latitude: [
          { required: true, message: '请输入传感器纬度信息', trigger: 'blur' }
        ]
      },
      selectedSensor: {
        id: '',
        type: '',
        longitude: '',
        latitude: ''
      },
      sensor_list: []
    }
  },
  methods: {
    addSensor () {
      this.dialogVisible1 = true
    },
    submitSensor (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 提交表单
          this.$http.post('http://127.0.0.1:8080/api/sensor/addSensor',
            {
              userid: this.user.id,
              id: this.newSensor.id,
              type: this.newSensor.type,
              longitude: this.newSensor.longitude,
              latitude: this.newSensor.latitude
            }, {
              headers: {
                'Content-Type': 'application/json'
              }
            }).then(function (res) {
            const resbody = res.body
            if (resbody.success) {
              alert(resbody.message)
              this.getSensorList()
            } else {
              alert(resbody.message)
            }
          }, function (res) {
            console.log(res.status)
          })
          this.dialogVisible1 = false
          console.log(this.newSensor)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetSensor (formName) {
      this.$refs[formName].resetFields()
    },
    sensorInfo (sensor, index) {
      this.dialog2Title = sensor.type + '传感器' + index + '基本信息'
      this.selectedSensor = sensor
      this.dialogVisible2 = true
    },
    submitChange () {
      this.dialogVisible2 = false
      console.log(this.selectedSensor)
    },
    selectSensor (sensor, index) {
      this.chartTitle = '传感器数据分析图' + '---' + sensor.type + '传感器' + index
      this.selectedSensor = sensor
      this.chartVisible = true
    },
    handleClose (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    getSensorList () {
      this.$http.get('http://127.0.0.1:8080/api/sensor/getSensorList',
        {
          params: {
            id: this.user.id
          }
        }).then(function (res) {
        // 解析返回数据
        const resbody = res.body
        const sensorlist = []
        console.log(resbody)
        for (var i = 0; i < resbody.length; i++) {
          sensorlist.push(resbody[i])
        }
        this.sensor_list = sensorlist
        console.log(this.sensor_list)
      }, function (res) {
        console.log(res.status)
      })
    }
  },
  mounted () {
    this.getSensorList()
  }
}
</script>

<style scoped>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  .el-aside {
    color: #333;
  }
</style>



