<template>
  <div id="register">
    <el-main id="main">
      <el-row type="flex" class="row-bg" justify="center">
        <el-col :xs="8" :sm="9" :md="8" :lg="6" :xl="5">
          <el-card class="box-card">
            <div solt="header" class="header">
              <span class="item">注册</span>
            </div>
            <div id="form">
              <!-- 注册表单 -->
              <el-form :model="register" status-icon ref="register">
                <el-form-item prop="username"
                              :rules="[{ required: true, message: '用户名不能为空'}]">
                  <el-input placeholder="请输入用户名" prefix-icon="el-icon-user"
                            v-model="register.username" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="password"
                              :rules="[{ required: true, message: '密码不能为空'}]">
                  <el-input placeholder="请输入密码" prefix-icon="el-icon-key" type="password"
                            v-model="register.password" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="repassword"
                              :rules="[{ required: true, message: '请再次输入密码'}]">
                  <el-input placeholder="请再次输入密码" prefix-icon="el-icon-key" type="password"
                            v-model="register.repassword"
                            @keyup.enter.native="registerEnter('register')"
                            auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button class="btn" type="primary" @click="submitForm('register')">注册</el-button>
                </el-form-item>
              </el-form>
              <div>
                <p><router-link to="/login" class="tips">已经有账号？点我去登录</router-link></p>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-main>
  </div>
</template>

<script>
export default {
  name: 'Register',
  data () {
    return {
      checked: false,
      register: {
        username: '',
        password: '',
        repassword: ''
      }
    }
  },
  methods: {
    submitForm (register) {
      if (this.register.repassword !== this.register.password) {
        // 弹出错误信息框
        this.$emit(
          'submit-form',
          this.$message({
            message: '两次输入的密码不相同',
            type: 'warning',
            duration: 6000
          })
        )
        // 清空表单状态
        this.$refs[register].resetFields()
      } else {
        this.$refs[register].validate((valid) => {
          if (valid) {
            // 提交表单
            this.$http.post('http://127.0.0.1:8080/api/user/register',
              {
                name: this.register.username,
                password: this.register.password
              }, {
                headers: {
                  'Content-Type': 'application/json'
                }
              }).then(function (res) {
              // 解析返回数据
              const resbody = res.body
              if (resbody.success) {
                // 弹出成功信息框
                this.$emit(
                  'submit-form',
                  this.$message({
                    message: resbody.message,
                    type: 'success',
                    duration: 6000
                  })
                )
              } else {
                // 弹出错误信息框
                this.$emit(
                  'submit-form',
                  this.$message({
                    message: resbody.message,
                    type: 'warning',
                    duration: 6000
                  })
                )
              }
            }, function (res) {
              console.log(res.status)
            })
          }
        })
      }
    },
    registerEnter (register) {
      this.submitForm(register)
    }
  }
}
</script>

<style scoped>
  body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  }

  #register {
    background: url("../assets/login.png");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -999;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  #main {
    font-family: Helvetica, sans-serif;
    margin-top: 8%;
    text-align: center;
  }

  .header {
    border-bottom: 1px solid #ebeef5;
    margin-bottom: 20px;
    padding: 15px 20px;
  }

  .header > .item {
    font-size: 23px;
    color: #3a8ee6;
  }

  .btn {
    width: 100%;
    border-radius: 28px;
  }

  #form {
    padding: 0 30px;
  }

  .check {
    float: left;
  }

  .tips {
    font-size: 13px;
    text-align: right;
    color: #409EFE;
    text-decoration: none;
  }

  .tips:hover {
    color: #3a8ee6;
  }
</style>



