<template>
  <div>
    <div class="block">
      <el-date-picker
        v-model="time_value"
        type="date"
        placeholder="选择日期"
        format="yyyy 年 MM 月 dd 日"
        value-format="yyyy-MM-dd"
        :picker-options="pickerOptions"
        @change="selectTime">
      </el-date-picker>
    </div>
    <div id="my_chart" :style="{width: '800px', height: '300px'}"></div>
  </div>

</template>

<script>
import echarts from 'echarts'
export default {
  name: 'LineChart',
  props: ['sensor'],
  data () {
    return {
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{
          text: '今天',
          onClick (picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '昨天',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '一周前',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }]
      },
      time_value: '',
      sensor_data: []
    }
  },
  methods: {
    selectTime () {
      // console.log(this.sensor)
      // console.log(this.time_value)
      // 提交表单
      this.$http.get('http://127.0.0.1:8080/api/sensor/getSensorData',
        {
          params: {
            id: this.sensor.id,
            date: this.time_value
          }
        }).then(function (res) {
        // 解析返回数据
        const resbody = res.body
        // console.log(resbody)
        const datalist = []
        for (var i = 0; i < resbody.length; i++) {
          datalist.push([resbody[i].data, resbody[i].time])
        }
        // console.log(datalist)
        this.sensor_data = datalist
        // 绘图
        // console.log(this.sensor_data)
        this.drawLineChart()
      }, function (res) {
        console.log(res.status)
      })
    },
    drawLineChart () {
      const dom = document.getElementById('my_chart')
      const myChart = echarts.init(dom, 'light', {renderer: 'svg'})
      myChart.setOption({
        title: {
          text: ''
        },
        tooltip: {},
        grid: {
          left: '5%',
          right: '5%',
          top: '10%',
          bottom: '10%'
        },
        dataset: {
          source: this.sensor_data
        },
        xAxis: {
          type: 'time'
        },
        yAxis: {},
        series: [{
          type: 'line',
          encode: {x: 1, y: 0}
        }]
      })
    }
  },
  mounted () {
    this.drawLineChart()
  },
  watch: {
    sensor: function () {
      this.sensor_data = []
      this.time_value = ''
      this.drawLineChart()
    }
  }
}
</script>

<style scoped>

</style>



